@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.user-info{display:flex;justify-content:flex-end;align-self:center}.user-info-avatar{width:50px;height:50px}.user-info-details{color:#8c8c8c;align-self:center;padding:10px;font-size:16px;font-family:'Open Sans'}@media (max-width: 936px){.user-info{grid-template-rows:repeat(4, auto);grid-template-columns:none;grid-row-gap:20px}}

.may-header{width:100%}.may-header ul{list-style:none;padding:0;margin:0}.may-header li{padding:8px 12px}.may-header li:hover{background-color:rgba(0,0,0,0.14);cursor:pointer}.may-header .dropdown-content{background-color:white;position:absolute;top:100%;min-width:160px;z-index:2}.may-header .dropdown-content-toggled{border:1px solid #e3e3e3;border-radius:0 0 0.25rem 0.25rem;background-color:white;position:absolute;top:100%;min-width:160px;z-index:2}.may-header .dropdown-wrapper{position:relative}.may-header-wrapper{width:80%;margin:0 auto}.may-header-logo{height:50px}.may-header-search-form{width:40%}.may-header-input{width:40%;border:1px solid #e7e7e7;background:#f7f6f6;border-radius:5%;font-family:'FontAwesome', 'sans-serif';padding:1px 10px}.may-header ::-webkit-input-placeholder{text-align:right}.may-header .toggler{border:none;outline:none;background:none;position:relative}.may-header-dark{width:100%;background-color:#303841}.may-header-dark ul{list-style:none;padding:0;margin:0}.may-header-dark li{padding:8px 12px}.may-header-dark li:hover{background-color:rgba(0,0,0,0.14);cursor:pointer}.may-header-dark .dropdown-content{background-color:white;position:absolute;top:100%;min-width:160px;z-index:2}.may-header-dark .dropdown-content-toggled{border:1px solid #e3e3e3;border-radius:0 0 0.25rem 0.25rem;background-color:white;position:absolute;top:100%;min-width:160px;z-index:2}.may-header-dark .dropdown-wrapper{position:relative}.may-header-dark-wrapper{width:80%;margin:0 auto}.may-header-dark-logo{height:50px}.may-header-dark-search-form{width:40%}.may-header-dark-input{width:40%;border:1px solid #e7e7e7;background:#f7f6f6;border-radius:5%;font-family:'FontAwesome', 'sans-serif';padding:1px 10px}.may-header-dark ::-webkit-input-placeholder{text-align:right}.may-header-dark .toggler{border:none;outline:none;background:none;position:relative}

.card-block{padding:0 10px}

.card-text{text-overflow:ellipsis;overflow:hidden;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical}

.genre-info-block{padding:0 10px;list-style:none}

.card{box-shadow:-1px 3px 9px -4px rgba(0,0,0,0.75);border:none}.card-title{text-align:left;padding-left:10px;margin-top:8px}.card-text{margin-top:10px}.card-dark{background-color:#303841;color:white}

.pagination-content{display:flex;justify-content:center;margin:0 auto}.pagination-wrapper{margin-top:20px}.pag-dark{background-color:#303841;color:white}

@media (min-width: 34em){.card-columns{-webkit-column-count:1;column-count:1}}@media (min-width: 48em){.card-columns{-webkit-column-count:2;column-count:2}}@media (min-width: 62em){.card-columns{-webkit-column-count:3;column-count:3}}@media (min-width: 75em){.card-columns{-webkit-column-count:4;column-count:4}}.container-wrapper-white{background-color:#f0efee;padding-top:20px}.container-wrapper-dark{background-color:#222831;padding-top:20px}

*{box-sizing:border-box;margin:0}html,body{margin:0;background:white;font-family:'Montserrat', helvetica, arial, sans-serif;font-size:14px;font-weight:400}.link{display:block;text-align:center;color:#777;text-decoration:none;padding:10px}.movie_card{position:relative;display:block;width:800px;margin:0 auto;overflow:hidden;border-radius:10px;transition:all 0.4s;box-shadow:0px 0px 120px -25px rgba(0,0,0,0.5)}.movie_card:hover{transform:scale(1.02);box-shadow:0px 0px 80px -25px rgba(0,0,0,0.5);transition:all 0.4s}.movie_card .info_section{position:relative;width:100%;height:100%;background-blend-mode:multiply;z-index:2;border-radius:10px}.movie_card .info_section .movie_header{position:relative;padding:25px;height:40%}.movie_card .info_section .movie_header h1{color:black;font-weight:400}.movie_card .info_section .movie_header h4{color:#555;font-weight:400}.movie_card .info_section .movie_header .minutes{display:inline-block;margin-top:15px;color:#555;padding:5px;border-radius:5px;border:1px solid rgba(0,0,0,0.05)}.movie_card .info_section .movie_header .type{display:inline-block;color:#959595;margin-left:10px}.movie_card .info_section .movie_header .locandina{position:relative;float:left;margin-right:20px;height:120px;box-shadow:0 0 20px -10px rgba(0,0,0,0.5)}.movie_card .info_section .movie_desc{padding:25px;height:50%}.movie_card .info_section .movie_desc .text{color:#545454}.movie_card .info_section .movie_social{height:10%;padding-left:15px;padding-bottom:20px}.movie_card .info_section .movie_social ul{list-style:none;padding:0}.movie_card .info_section .movie_social ul li{display:inline-block;color:rgba(0,0,0,0.3);transition:color 0.3s;transition-delay:0.15s;margin:0 10px}.movie_card .info_section .movie_social ul li:hover{transition:color 0.3s;color:rgba(0,0,0,0.7)}.movie_card .info_section .movie_social ul li i{font-size:19px;cursor:pointer}.movie_card .blur_back{position:absolute;top:0;z-index:1;height:100%;right:0;background-size:cover !important;border-radius:11px}@media screen and (min-width: 768px){.movie_header{width:65%}.movie_desc{width:50%}.info_section{background:linear-gradient(to right, #e5e6e6 50%, transparent 100%)}.blur_back{width:80%;background-position:-100% 10% !important}}@media screen and (max-width: 768px){.movie_card{width:95%;margin:0 auto;min-height:350px;height:auto}.blur_back{width:100%;background-position:50% 50% !important}.movie_header{width:100%;margin-top:85px}.movie_desc{width:100%}.info_section{background:linear-gradient(to top, #e5e6e6 50%, transparent 100%);display:inline-grid}}

.movie-card-wrapper-dark{background-color:#222831;padding:70px 0 180px 0}.movie-card-wrapper{padding:70px 0 180px 0}

